/* For the parent container which wraps around your .auth-container, which in your case might be the <body> or a main <div> in your App component */
.auth-context {
    height: 100vh;
    margin: 0;
    display: flex;
    align-items: center; /* This will center children vertically */
    justify-content: center; /* This will center children horizontally */
}

/* For the auth container */
.auth-container {
    /* ... other styles ... */
    width: 300px; /* or any desired width */
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Optional: Adds a subtle shadow for better visual distinction */
    border-radius: 4px; /* Optional: Rounds the corners for aesthetics */
}




/* Container styling */
.auth-container {
    background-color: #2e2e2e;
    color: #ffffff;
    padding: 20px;
    width: 300px;
    margin: 0 auto;
    border-radius: 4px;
  }
  
  /* Header styling */
  .auth-container h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  /* Input fields styling */
  .auth-container input {
    box-sizing: border-box;
    display: block;
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ffffff;
    background-color: #4a4a4a;
    color: #ffffff;
    border-radius: 4px;
    transition: background-color 0.3s;
  }
  
  .auth-container input::placeholder {
    color: #bbbbbb;
  }
  
  .auth-container input:focus {
    outline: none;
    background-color: #555555;
  }
  
  /* Button styling */
  .auth-container button {
    display: block;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #4a4a4a;
    color: #ffffff;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .auth-container button:hover {
    background-color: #555555;
  }
  
  /* Message styling */
  .auth-container p {
    text-align: center;
    margin-top: 20px;
  }