.movie-container {
    font-family: Arial, sans-serif;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    height: 100vh;
    overflow: auto; 
    background-attachment: fixed;
    background-size: cover;      /* Ensure the image covers the entire container */
    background-position: center; /* Center the image */
    background-repeat: no-repeat;
  }

  body, html {
    height: 100%;
    margin: 0;
    overflow: hidden;  /* Prevents the whole page from scrolling */
}

  
  .movie-poster {
    width: 100%;
    max-height: 500px;
    object-fit: cover; /* This ensures the image keeps its aspect ratio */
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  .movie-detail {
    max-height: 100vh;
    overflow-y: auto;
    background-color: (0, 0, 0, 0);
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  .movie-info h2, .movie-info h4 {
    margin: 0;
    padding: 0;
  }
  
  .overview {
    margin: 10px 0;
  }
  
  .meta-info, .genres, .languages, .production {
    margin-bottom: 10px;
  }
  
  .similar-movies {
    overflow-x: auto;
    white-space: nowrap;
    padding: 10px 0;
    background-color: rgba(0, 0, 0, 0);
  }
  
  /* Assuming similar movie items have a class name .similar-movie-item */
  .similar-movie-item {
    display: inline-block;
    width: 150px; /* adjust this width based on your needs */
    margin-right: 10px;
  }
  