/* Movie.css */
.movie-card {
  background-color: #393838;
  position: relative;
  max-width: 200px;
  max-height: 260px;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  margin: 15px;
  overflow: hidden;
  transition: transform 0.3s;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.movie-image {
  flex: 1;
  max-width: 100%;
  /* Adjust as needed */
}

.movie-image img {
  width: 100%;
  height: auto;
}

.movie-card:hover {
  transform: scale(1.05);
  /* Optional: Slight scale on hover for effect */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.movie-card img {
  display: block;
  width: 100%;
  height: auto;
}

.movie-details {
  background-color: #3b3a3a;
  /* You can set this to any color you like */
  flex: 2;
  padding: 10px;
}

.movie-details h3 {
  margin: 0;
  margin-bottom: 8px;
  font-size: 1.2em;
}

.movie-details p {
  margin: 0;
  color: #666;
}

.movie-card .favorite-icon {
  position: absolute;
  top: 10px;
  /* Adjust this value to position the heart vertically */
  right: 10px;
  /* Adjust this value to position the heart horizontally */
  z-index: 2;
  /* Ensures the icon is on top of the image */
  width: 24px;
  /* Adjust the width as needed */
  height: 24px;
  /* Adjust the height as needed */
}