/* MovieList.css */
.movie-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; /* By default, show three movies in a row */
  gap: 20px; /* Space between grid items */
  padding: 20px;
}

/* For tablets and mobile responsiveness */
@media (max-width: 900px) {
  .movie-list {
    grid-template-columns: 1fr 1fr; /* Show two movies in a row for medium screens */
  }
}

@media (max-width: 600px) {
  .movie-list {
    grid-template-columns: 1fr; /* Show one movie in a row for small screens */
  }
}
